<template lang="pug">
  div
    v-md-editor.github-markdown-body(:value="text" mode="preview")
    div.demo-content
      tableSearch(@submit="search" :need-reset="true" @reset="reset" :showMoreCondition="false")
        div(slot="condition")
          el-form-item(label="查询条件")
            el-input(v-model="formData.a" size="small" placeholder="请输入" clearable)
    v-md-editor.github-markdown-body(:value="text2" mode="preview")
    div.demo-content
      search-group(
        :need-reset="true",
        :lableWidth="80",
        @submit="search",
        @reset="reset",
      )
        div(slot="condition")
          el-form-item(label="查询条件")
            el-input(v-model="formData.x1" size="small" placeholder="请输入" clearable)
          el-form-item(label="查询条件")
            el-input(v-model="formData.x2" size="small" placeholder="请输入" clearable)
          el-form-item(label="查询条件")
            el-input(v-model="formData.x3" size="small" placeholder="请输入" clearable)
          el-form-item(label="是否")
            el-select(v-model="formData.x4" size="small" placeholder="请选择" clearable)
              el-option( label="是" :value="1")
              el-option( label="否" :value="2")
</template>

<script>
import easyTable from "@/components/easyTable/EasyTable";

export default {
  components: {
    easyTable,
  },
  data() {
    return {
      formData: {},
      text: `
  ### 查询栏
  #### TableSearch基础用法
  \`查询条件\`和\`按钮\`左右分布
  - \`submit\`：点击查询按钮触发，自定义处理逻辑
  - \`need-reset\`：是否显示重置按钮
  - \`reset\`：点击重置按钮触发，自定义处理逻辑
  - \`showMoreCondition\`：查询条件超过一行是否自动展开

  使用\`TableSearch\`组件只需要使用\`slot="condition"\`引入插槽，在内部定义\`el-form-item\`表单项即可。
  > ps：因为重置不是单纯把入参置空，而是把入参重置到\`初始化\`状态，比如时间条件，重置为最近一周，所以需要自定义重置逻辑。
  #### 示例
  \`\`\`html
  tableSearch(@submit="search" :need-reset="true" @reset="reset" :showMoreCondition="false")
  \tdiv(slot="condition")
  \t\tel-form-item(label="查询条件")
  \t\t\tel-input(v-model="formData.a" size="small" placeholder="请输入" clearable)
  \`\`\`
      `,
      text2: `
  #### SearchGroup基础用法

  \`查询条件\`和\`按钮\`上下分布
  - \`lableWidth\`：设置查询条件文案长度。
  其他属性参考\`TableSearch\`
  > tip：不支持查询条件伸缩，默认平铺

  #### 示例
  \`\`\`html
  search-group(
    :need-reset="true",
    :lableWidth="80",
    @submit="search",
    @reset="reset",
  )
  div(slot="condition")
  \tel-form-item(label="查询条件")
    \tel-input(v-model="formData.x1" size="small" placeholder="请输入" clearable)
    el-form-item(label="查询条件")
    \tel-input(v-model="formData.x2" size="small" placeholder="请输入" clearable)
    el-form-item(label="查询条件")
    \tel-input(v-model="formData.x3" size="small" placeholder="请输入" clearable)
    el-form-item(label="是否")
      el-select(v-model="formData.x4" size="small" placeholder="请选择" clearable)
      \tel-option( label="是" :value="1")
        el-option( label="否" :value="2")
  \`\`\`

      `,
    };
  },
  methods: {
    search() {},
    reset() {
      this.formData = {};
    },
  },
}
</script>

